___CSS_LOADER_EXPORT___.push([module.id, `* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.site-wrapper {
  padding: 12px;
  width: 60%;
  margin: auto;
  display: flex;
  flex-direction: column;
  gap: 35px;
}

button {
  padding: 2px 8px;
  font-size: 18px;
  background: transparent;
  box-shadow: 1px 1px 1px 1px teal;
  transition-duration: 0.3s;
  font-weight: 600;
}

button:hover {
  box-shadow: 2px 3px 1px 2px teal;
  font-size: 1.1rem;
  border: 2px solid white;
  background-color: teal;
  color: white;
  font-weight: 600;
}

.main {
  display: flex;
  flex-direction: column;
  gap: 50px;
}
.main .recent-score-search {
  display: flex;
  gap: 12px;
  align-items: center;
}
.main .board-wrapper {
  display: flex;
  justify-content: space-between;
  gap: 25px;
}
.main .right-section {
  display: flex;
  flex-direction: column;
  gap: 20px;
  width: 40%;
}
.main .right-section form {
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  gap: 25px;
}
.main .right-section form input {
  height: 25px;
  width: 100%;
  padding: 0 5px;
}
.main .right-section form button {
  width: auto;
}
.main .left-section {
  display: flex;
  flex-direction: column;
  gap: 12px;
  width: 70%;
}
.main .left-section .score-board-wrapper {
  border: 2px solid teal;
  padding: 12px 0;
}
.main .left-section .score-board-wrapper .score-list {
  display: flex;
  flex-direction: column;
  gap: 5px;
}
.main .left-section .score-board-wrapper .score-list .name-score {
  list-style-type: none;
  display: flex;
  gap: 12px;
  padding: 5px 12px;
}
.main .left-section .score-board-wrapper .score-list .name-score:nth-child(odd) {
  font-weight: 800;
  font-size: 1.2rem;
  color: rgb(6, 72, 72);
}
.main .left-section .score-board-wrapper .score-list .name-score:nth-child(even) {
  background-color: teal;
  color: white;
  font-weight: 800;
  font-size: 1.2rem;
}

.header {
  display: flex;
  flex-direction: column;
  background-color: teal;
  padding: 12px;
}
.header .announcement-bar {
  justify-content: center;
  text-align: center;
}
.header .announcement-bar span {
  color: white;
  font-size: 18px;
  font-weight: 700;
}
.header .elements {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.header .elements span {
  font-size: 50px;
  color: white;
}
.header .elements .cancel-icon {
  font-size: 30px;
  font-weight: 900;
}
.header .elements .left-right-arrow-wrapper {
  display: flex;
  gap: 12px;
}
.header .elements .search-field {
  width: 60%;
}
.header .elements .search-field input {
  width: 100%;
  height: 35px;
  padding: 0 8px;
}
.header .elements .search-icon span {
  font-size: 1.5rem;
  background-color: white;
  padding: 5px 22px;
  border-radius: 25px;
  -webkit-border-radius: 25px;
  -moz-border-radius: 25px;
  -ms-border-radius: 25px;
  -o-border-radius: 25px;
}

.msg {
  font-size: 1.4rem;
  font-weight: 600;
  text-align: center;
}

h1 {
  font-size: 40px;
}

.p1 {
  font-size: 24px;
  font-weight: 600;
}`, "",{"version":3,"sources":["webpack://./src/styles/style.scss"],"names":[],"mappings":"AACA;EACE,SAAA;EACA,UAAA;EACA,sBAAA;AAAF;;AAGA;EACE,aAAA;EACA,UAAA;EACA,YAAA;EACA,aAAA;EACA,sBAAA;EACA,SAAA;AAAF;;AAGA;EACE,gBAAA;EACA,eAAA;EACA,uBAAA;EACA,gCAAA;EACA,yBAAA;EACA,gBAAA;AAAF;;AAGA;EACE,gCAAA;EACA,iBAAA;EACA,uBAAA;EACA,sBAAA;EACA,YAAA;EACA,gBAAA;AAAF;;AAGA;EACE,aAAA;EACA,sBAAA;EACA,SAAA;AAAF;AAEE;EACE,aAAA;EACA,SAAA;EACA,mBAAA;AAAJ;AAGE;EACE,aAAA;EACA,8BAAA;EACA,SAAA;AADJ;AAIE;EACE,aAAA;EACA,sBAAA;EACA,SAAA;EACA,UAAA;AAFJ;AAII;EACE,aAAA;EACA,sBAAA;EACA,qBAAA;EACA,SAAA;AAFN;AAIM;EACE,YAAA;EACA,WAAA;EACA,cAAA;AAFR;AAKM;EACE,WAAA;AAHR;AAQE;EACE,aAAA;EACA,sBAAA;EACA,SAAA;EACA,UAAA;AANJ;AAQI;EACE,sBAAA;EACA,eAAA;AANN;AAQM;EACE,aAAA;EACA,sBAAA;EACA,QAAA;AANR;AAQQ;EACE,qBAAA;EACA,aAAA;EACA,SAAA;EACA,iBAAA;AANV;AASQ;EACE,gBAAA;EACA,iBAAA;EACA,qBAAA;AAPV;AAUQ;EACE,sBAAA;EACA,YAAA;EACA,gBAAA;EACA,iBAAA;AARV;;AAeA;EACE,aAAA;EACA,sBAAA;EACA,sBAAA;EACA,aAAA;AAZF;AAcE;EACE,uBAAA;EACA,kBAAA;AAZJ;AAcI;EACE,YAAA;EACA,eAAA;EACA,gBAAA;AAZN;AAgBE;EACE,aAAA;EACA,8BAAA;EACA,mBAAA;AAdJ;AAgBI;EACE,eAAA;EACA,YAAA;AAdN;AAiBI;EACE,eAAA;EACA,gBAAA;AAfN;AAkBI;EACE,aAAA;EACA,SAAA;AAhBN;AAmBI;EACE,UAAA;AAjBN;AAmBM;EACE,WAAA;EACA,YAAA;EACA,cAAA;AAjBR;AAsBM;EACE,iBAAA;EACA,uBAAA;EACA,iBAAA;EACA,mBAAA;EACA,2BAAA;EACA,wBAAA;EACA,uBAAA;EACA,sBAAA;AApBR;;AA0BA;EACE,iBAAA;EACA,gBAAA;EACA,kBAAA;AAvBF;;AA0BA;EACE,eAAA;AAvBF;;AA0BA;EACE,eAAA;EACA,gBAAA;AAvBF","sourcesContent":["\r\n* {\r\n  margin: 0;\r\n  padding: 0;\r\n  box-sizing: border-box;\r\n}\r\n\r\n.site-wrapper {\r\n  padding: 12px;\r\n  width: 60%;\r\n  margin: auto;\r\n  display: flex;\r\n  flex-direction: column;\r\n  gap: 35px;\r\n}\r\n\r\nbutton {\r\n  padding: 2px 8px;\r\n  font-size: 18px;\r\n  background: transparent;\r\n  box-shadow: 1px 1px 1px 1px teal;\r\n  transition-duration: 0.3s;\r\n  font-weight: 600;\r\n}\r\n\r\nbutton:hover {\r\n  box-shadow: 2px 3px 1px 2px teal;\r\n  font-size: 1.1rem;\r\n  border: 2px solid white;\r\n  b